import {
    FacebookShareButton,
    FacebookIcon,
    TwitterShareButton,
    TwitterIcon
} from 'react-share';

const Calc = (props) =>{
    
    //props
    let yourLivePrefProps = props.todoufuken[props.yourLivePrefecture];
    let comparePrefProps = props.todoufuken[props.comparePrefecture];

    //
    let compareSalary;
    if(comparePrefProps && yourLivePrefProps){
        let yourLivePrefDiff = props.salary/yourLivePrefProps.salary;
        compareSalary = parseFloat(comparePrefProps.salary * yourLivePrefDiff).toFixed(1);
    }

    //shareURL
    let anchor = document.createElement("a");
    anchor.href = `?salary=${props.salary}&yourLivePrefecture=${props.yourLivePrefecture}&comparePrefecture=${props.comparePrefecture}`;

    //share
    const shareText =`${yourLivePrefProps ? yourLivePrefProps.name :"XX"}${props.salary ? props.salary:" XXX " }${comparePrefProps ? comparePrefProps.name:"XX"}${compareSalary ? compareSalary:"XXX"}`

    return(

        <div id="calc_result_main">
            <p className="compare_result"></p>
            {yourLivePrefProps && <span className="strong_result">{yourLivePrefProps.name}</span>}<p className="strong_result">{props.salary ? props.salary:" XXX " }</p>
            {comparePrefProps && <span className="strong_result">{comparePrefProps.name}</span>}
            <p className="strong_result">{compareSalary ? compareSalary:"XXX"}</p>
            <div id="share">
                <FacebookShareButton url={[anchor.href]} quote={[shareText]}>
                    <FacebookIcon size={32} round />
                </FacebookShareButton>
                <TwitterShareButton url={[anchor.href]} title={[shareText]}>
                    <TwitterIcon size={32} round />
                </TwitterShareButton>
            </div>

            <div className="bottom_caution">
                <p>https://www.mhlw.go.jp/toukei/itiran/roudou/chingin/kouzou/z2021/index.html
<br/>
                </p>
            </div>
        </div>
    );
};

export default Calc;